<template>
	<div class="main">
		<!-- <mt-navbar id="navBar"  v-model="selected">
	    	<mt-tab-item id="c"></mt-tab-item>
	    </mt-navbar> -->
		<mt-tab-container v-model="selected"> 
		  <mt-tab-container-item id="c">
			<!--  -->
			  <mt-loadmore 
			  	:bottomMethod="carLoadBottom" 
			  	:auto-fill = "false"
			  	:bottomAllLoaded = "carisLastPage"
				  ref="loadmore"
			  >
			  	<Bill :bill-info="carBillInfo" @itemClick="itemClick"></Bill>
			  </mt-loadmore>
			  <div style="widtt:100%;height:0.92rem;"></div>
			  	<div class="btn-fixed">
		    		<div class="fl select-btn" v-show="quan" :class="{allSelected:carAllselect }" @click="allSelect(carBillInfo,'carAllselect')">&nbsp;</div>
		    		<div class="pay" @click="pay('carBillInfo','carAllPrice','carAllselect')">
		    			
		    			<span>
		    				{{carAllPrice}}
		    			</span>
		    		</div>
        		</div>
			<!--  -->
		  </mt-tab-container-item>
		  
		</mt-tab-container>
	</div>
</template>
<script>
	let vm;
	import wx from 'weixin-js-sdk';
	import Bill from '../../components/bill.vue';
	export default {
		components:{Bill},
		computed:{
			//
		  	carAllPrice : function(){
		  		let ap = 0;
		  		for(let i in this.carBillInfo){
		  			if(this.carBillInfo[i].selected == true){
		  				ap+=Number(this.carBillInfo[i].fee_price)
		  			}
		  		}
		  		return parseFloat(ap).toFixed(2)
		    },

		},
		data(){
			return {
				url : '/billList',
				stmtId:'',// 
		  		params : {
		  			startDate:'',
		  			endDate:'',
		  			payStatus: '02', //
		  			currentPage : 1, //
		  			totalCount : 0, //
		  		},
				carisLastPage:false,//
				carAllselect:false,//
				carBillInfo:[],//
				selected:'c', // 
				carBillPage :1, //
				reduceMode:1,
				quan:false,
				baseUrl:'https://test.e-shequ.com/weixin/',
			}
		},
		created(){
		  	vm = this;
		},
		mounted(){
			this.common.checkRegisterStatus();
			//
		  	let url = location.href.split('#')[0]
		  	vm.receiveData.wxconfig(vm,wx,['scanQRCode'],url);
		  	
		  	//   
		  	vm.receiveData.getData(vm, vm.url, 'data',function(){
		  		vm.billInfo = vm.data.result.bill_info;//
		  		vm.reduceMode = vm.data.result.reduce_mode;//
		  		vm.carBillInfo = vm.data.result.car_bill_info;//
		  		vm.pay_least_month = vm.data.result.pay_least_month; // 
		  	},vm.params) ;
		  	
		},
		methods:{
			carLoadBottom(){//
		  		//
		  		let tempArr = null;
		  		// 
		  		vm.carBillPage+=1
				vm.params.currentPage = vm.carBillPage;
				//
				vm.receiveData.getData(
					vm,
					vm.url,
					'pageData2',
					function(){
		  				tempArr = vm.pageData2.result.car_bill_info;//
		  				if(tempArr && tempArr.length > 0){
		  					vm.carBillInfo =vm.carBillInfo.concat(tempArr) //
		  					vm.carAllselect = false;
		  				}else{
							  vm.carisLastPage = true;
							  vm.quan=true;
		  				}
		  			},vm.params)
		  		 vm.$refs.loadmore.onBottomLoaded()
		  			
		  	},
		  	//
			  pay(list,allPrice,allselect){//    , string
		  		this.common.checkRegisterStatus();
		  		if( vm[allPrice] < 0.01){
		  			alert('');
		  			return
		  		}
		  		let selectedArr = [];
		  		var sel_bill_arr = new Array();
		  		if(vm[allselect] == true){
		  			//
		  			//sel_bill_arr = vm[list];
		  			// var ret = jQuery.inArray(vm[list][i].service_fee_cycle,sel_bill_arr);
		  			// if(-1 == ret){
		  			// 	sel_bill_arr.push(vm[list][i].service_fee_cycle);
		  			// }
		  			// selectedArr.push(vm[list][i])
		  			for(let i in vm[list]){
		  				var ret = jQuery.inArray(vm[list][i].service_fee_cycle,sel_bill_arr);
			  			if(-1 == ret){
			  				sel_bill_arr.push(vm[list][i].service_fee_cycle);
			  			}	
	  					selectedArr.push(vm[list][i])
		  			}
		  			
		  		}else{
		  			//
		  			for( let i in vm[list]){
		  				if(vm[list][i].selected){
		  				var ret = jQuery.inArray(vm[list][i].service_fee_cycle,sel_bill_arr);
				  			if(-1 == ret){
				  				sel_bill_arr.push(vm[list][i].service_fee_cycle);
				  			}	
		  					selectedArr.push(vm[list][i])
		  				}
		  			}
		  		}
		  		let bills="";
		  		let pay_addr = selectedArr[0].pay_addr;
		  		//let pay_addr = sel_bill_arr[0].pay_addr;
		  		for(let i in selectedArr){
		  			if(selectedArr.length - 1 == i){
		  				bills+=selectedArr[i].bill_id;
		  			}else{
		  				bills+=selectedArr[i].bill_id+',';	
		  			}	  			
		  		}
		  		if(vm.pay_least_month>0){
					if(vm.pay_least_month>sel_bill_arr.length){
						alert(""+vm.pay_least_month+"");
						return false;
					}
				}
		  		//------
		  		vm.str = 'https://www.e-shequ.com/shijiazhuang/weixin/';
		  		let url = vm.str + "paymentdetail.html?#/?billIds="+bills+"&stmtId="+vm.stmtId+"&payAddr="+escape(pay_addr)+"&totalPrice="+vm[allPrice]+"&reduceMode="+vm.reduceMode;
	            window.location.href = url;


	     		//-------
	     //        vm.str = 'https://www.e-shequ.com/pay/';
		  		// vm.baseUrl='https://www.e-shequ.com/weixin/';
		  		// var url = vm.str+"hx";
	     //        url += "paymentdetail.html?#/?billIds="+bills+"&stmtId="+vm.stmtId+"&payAddr="+escape(pay_addr)+"&totalPrice="+vm[allPrice]+"&reduceMode="+vm.reduceMode;
	     //        url += "&basePageUrl="+vm.baseUrl;
	     //        window.location.href = url;
	        
		  	},
		  	// params1: params2:
		  	itemClick:function(index,b){//3 
		  		let len = b.length
		  		if(b[index].selected){//
		  			for(let i=index;i< len;i++){
		  				//
		  				vm.$set(b[i],'selected',false);	
		  			}
		  			//
	 	  			vm.carAllselect = false;
		  		}else{//
		  			for(let j=index;j >= 0;j--){//
		  				vm.$set(b[j],'selected',true);	
		  			}
		  		}
		  	},

		  	// params:
		  	allSelect:function(arr,a){
		  		if(vm[a] ){//
		  			for(let i in arr){
		  				vm.$set(arr[i],'selected',false);
		  			}
		  		}else{//
		  			for(let i in arr){
		  				vm.$set(arr[i],'selected',true);
		  			}
		  		}
		  		//
		  		vm[a] = !vm[a];
		  	},
		}
	}
</script>

<style scoped>
a{color:black}
/**/

.query-data{
	height: 100%;
	padding: 0.25rem 1.2rem;
	margin-bottom: 0.2rem;
}
.virtual-input{
	width:3.4rem;
	border:1px solid #4c4c4c;
	border-radius: 5px;
	padding-left: 0.2rem;
	display: inline-block;
	height: 0.5rem;
}
.input-row{
	padding-bottom: 0.3rem;
	font: 0.26rem/0.5rem "";
	color: #a6937c;
	height: 0.5rem;
}
.top2{
	position: relative;
	top: -0.2rem;

}

.btn-fixed{
	position: fixed;
	color: #fff;
    left: 0;
    right: 0;
    bottom: 0;
    height: 0.92rem;
    line-height: 0.92rem;
    text-align: center;
}

.select-btn{
	padding-left: 36px;
    background: url('../../assets/images/pay/icon_unselect_white.png') no-repeat;
    background-color: rgba(0,0,0,0.6);
    background-size: 16px;
    background-position: 15px center;
    height: 0.92rem;
    line-height: 0.92rem;
}

.allSelected{
	background: url('../../assets/images/pay/icon_selected_white.png') no-repeat;
	 background-color: rgba(0,0,0,0.6);
    background-size: 16px;
    background-position: 15px center;

}

.pay{
	overflow: hidden;
	background: #ff8a00;
	text-align: center;	
}	

.main{
	margin:0 0.3rem;
}

 .mint-navbar .mint-tab-item{
 	border-bottom: 1px solid #cdcdcb;
 }

 .mint-navbar .mint-tab-item.is-selected{
 	border-bottom: 1px solid #ff8a00;
 	margin-bottom: 0;
 	color:#ff8a00;
 }
</style>


