import xml2js from'xml2js' 
var xmlParser = new xml2js.Parser({explicitArray : false, ignoreAttrs : true})
    //xmljson
import wx from 'weixin-js-sdk';


let receiveData = {



    /*
     * 
     * @param  {objec} vm     [Vue]
     * @param  {objec} wx     []
     * @param  {string} url     [url]
     * @param  {array} apilist    []
     */
    wxconfig(vm,wx,apilist,url){
        vm.axios.post('/getUrlJsSign', {url : url })
            .then(function (res) {
                let a = JSON.parse(res.data)
                let wd = a.result  //
                wx.config({
                    debug: false, // ,apialertpclogpc
                    appId: wd.appId, // 
                    timestamp: wd.timestamp, // 
                    nonceStr: wd.nonceStr, // 
                    signature: wd.signature,// 1
                    jsApiList: apilist // JSJS2
                });
            })
            .catch(function (err) {
                //alert('-config')
                console.log('fail', err);
        });
    },

    // chooseWXPay:function(vm,wx,backdataname){
    //     wx.chooseWXPay({
    //         timestamp: 0, // jssdktimestamptimeStampS
    //         nonceStr: '', //  32 
    //         package: '', // prepay_idprepay_id=\*\*\*
    //         signType: '', // 'SHA1''MD5'
    //         paySign: '', // 
    //         success: function (res) {
    //          alert
    //         }
    //     })
    // },

    /**/
    chooseImage:function(vm,wx,backdataname){
        wx.chooseImage({
            count: 1, // 9
            sizeType: ['original', 'compressed'], // 
            sourceType: ['album', 'camera'], // 
            success: function (res) {
                    var localIds = res.localIds; // IDlocalIdimgsrc
            },
            fail: function (res){
                console.log(" ")
            }
        });
    },


    /*
      * 
     * @param  {objec} vm     [Vue]
     * @param  {objec} wx     []
     * @param  {string} backdataname    []
    */
    scan:function(vm,wx,backdataname){
        wx.scanQRCode({
            needResult: 1, // 01
            scanType: ["qrCode","barCode"], // 
            success: function (res) {
                    //console.log(res.resultStr)
                     var rs = res.resultStr;
                     if(rs.indexOf('CODE')>=0) {
                        rs = rs.split(',')[1];
                     }
                        vm[backdataname] = rs; // needResult  1 
                },
            fail: function (res) {
                    //alert('-')
                    console.log(" ")
                    //alert(" ");
                }
        });
    },

    /*
     * axiosAPI,Vue
     * @param  {objec} vm     [Vue]
     * @param  {string} url    [ajax]
     * @param  {string} backdataname [ajaxvmkey]
     * @params {object} callback  []
     * @param  {object} params       []
     */
        //
    getData: function (vm, url, backdataname,callback,params) {
        if (typeof (params) == 'undefined' || typeof (params) != 'object') {
            params = {}
        };
        if (backdataname == '' || typeof (backdataname) != 'string') {
            backdataname = 'data';
        };
        vm.axios.get(url, {
                params: params
            })
            .then(function (res){
                let a = JSON.parse(res.data)
                vm[backdataname] = a
                if (typeof (callback) == 'function') {//
                    callback()
                    // switch (""+ vm[backdataname].errorCode) {
                    //     case "40001":
                    //         console.log(2)
                    //         common.dealWithAjaxData();
                    //         break;
                    //     case "40002":
                    //         toBindLink();
                    //         break;
                    //     case "42032":
                    //         common.wechatAuthorize();
                    //         break;
                    //     default:
                    //         // r(e)
                    //         console.log(1)
                    //         break;
                       
                    // }
                }
            })
            .catch(function (err) {
                //alert('-')
                console.log(err);
            })
    },
   /**
     * post
     * @param  {object} vm           [vue]
     * @param  {string} url          [ajax]
     * @param  {object} params       []
     * @param  {object} callback     []
     */
    postData: function (vm, url, params,backdataname,callback) {
        if (typeof (params) == 'undefined' || typeof (params) != 'object') {
            params = {}
        };
        vm.axios.post(url, params)
            .then(function (res) {

                let a = res.data;
                 vm[backdataname] = JSON.parse(a).result
                // xmlParser.parseString(a, function (err, result) {
                // //
                //  vm[backdataname] = result.BaseResult
                // });
                if (typeof (callback) == 'function') {//
                        callback()
                }

            })
            .catch(function (err) {
                if(vm.loadingShow){
                    vm.loadingShow = false;
                }
                if(err.message){
                    
                }              
                console.log('fail', err);
            });
    },
    //xml
    toxml:function(xmlStr){
        var root = document.createElement('XMLROOT');
        root.innerHTML = xmlStr
        return root
    },

    //json
    loadxml: function(xmlStr){
        
        var root = document.createElement('XMLROOT');
        root.innerHTML = xmlStr;
        return this.parse(root)
    },
    
    // xml json
    parse: function(node){
        var result = {};
        for(var i = 0 ; i < node.childNodes.length ; ++i){
            if(node.childNodes[i].nodeType == 1){// 
                result[node.childNodes[i].nodeName.toLowerCase()] = this.parse(node.childNodes[i]);
            }else if(node.childNodes[i].nodeType==3){// 
                return node.childNodes[i].nodeValue;
            }
        }
        return result;
    } ,



};

export default receiveData;


