import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'//

import wuye from '@/pages/index/index'//
import bill from '@/components/bill'
import suggest from '@/pages/suggest/suggest' //

//
import Myhouse from '@/pages/house/myhouse'
import AddHouse from '@/pages/house/add-house'
import BindHouse from '@/pages/house/bind-house'

//
import Pay from '@/pages/pay/pay'
import payStop from '@/pages/pay/pay-stop'

Vue.use(Router)

const router= new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
  	  children:[
        {path:'',component:wuye},
   	  ]
    },
    {
      path:'',
      name:'wuye',
      component:wuye
    },
    {
      path:'/suggest',
      name:'suggest',
      component:suggest
    },
    {
      path:'/bill',
      name:'bill',
      component:bill
    },
    {
      path:'/Myhouse',
      name:'Myhouse',
      component: Myhouse,
      meta:{
        title:''
      }
    },
    {
      path:'/addHouse',
      name:'AddHouse',
      component:AddHouse,
      meta:{
        title:''
      }
    },
    {
      path:'/bindHouse/:number',
      name:'BindHouse',
      component:BindHouse,
      meta:{
        title:''
      }
    },
    {
      path:'/Pay',
      name:'Pay',
      component:Pay,
      meta:{
        title:''
      }
    },
    {
      path:'/payStop',
      name:'payStop',
      component:payStop,
      meta:{
        title:''
      }
    }
  ]
});
//
// to  from next() 
router.beforeEach((to, from, next) => {
    //title
    changeTitle(to.meta.title)
    next();
});


//title
function changeTitle(title) {
    title = title ? title : '';
    window.document.title = title;
};

export default router



